/* ProfileCard.scss */

body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}

/* Main profile card container */
.profile-card {
  max-width: 800px;
  margin: 40px auto 20px auto;
  padding: 40px;
  box-sizing: border-box;
  
  @media (max-width: 768px) {
    margin: 50px 10px 20px 10px;
    padding: 15px;
  }
  
  @media (max-width: 480px) {
    margin: 45px 5px 20px 5px;
    padding: 10px;
  }
  
  @media (max-width: 320px) {
    margin: 40px 5px 20px 5px;
  }
}

/* Profile content layout */
.profile-content {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  gap: 20px;
  
  @media (max-width: 768px) {
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    gap: 15px;
  }
  
  @media (max-width: 480px) {
    gap: 10px;
  }
}

/* Text and icon alignment */
.text-info {
  display: flex;
  flex-direction: column;
  text-align: left;
  flex: 1;
  
  @media (max-width: 768px) {
    text-align: left;
    align-items: flex-start;
    max-width: 60%;
  }
  
  @media (max-width: 480px) {
    max-width: 55%;
  }
}

/* ===== ANIMATED PROFILE PICTURE STYLES ===== */

/* Container for the profile picture with animation */
.profile-picture-container {
  position: relative;
  width: 220px;
  height: 220px;
  flex-shrink: 0;
  order: 2;
  
  @media (max-width: 768px) {
    width: 120px;
    height: 120px;
  }
  
  @media (max-width: 480px) {
    width: 100px;
    height: 100px;
  }
  
  @media (max-width: 320px) {
    width: 80px;
    height: 80px;
  }
}

/* Image wrapper */
.image-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 255, 153, 0.3);
  z-index: 1;
}

.profile-picture {
  width: 100%;
  height: 100%;
  object-fit: cover;
  padding: block;
}

.svg-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 228px;
  height: 228px;
  z-index: 2;
  pointer-events: none;
  
  @media (max-width: 768px) {
    width: 126px;
    height: 126px;
  }
  
  @media (max-width: 480px) {
    width: 106px;
    height: 106px;
  }
  
  @media (max-width: 320px) {
    width: 86px;
    height: 86px;
  }
  
  svg {
    width: 100%;
    height: 100%;
  }
}

.rotating-circle {
  animation: rotate 12s linear infinite, pulse 3s ease-in-out infinite;
  transform-origin: center;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes pulse {
  0%, 100% {
    stroke-dasharray: 15 41 87 68;
  }
  25% {
    stroke-dasharray: 25 31 77 78;
  }
  50% {
    stroke-dasharray: 10 51 67 83;
  }
  75% {
    stroke-dasharray: 30 26 92 63;
  }
}

/* ===== END ANIMATED STYLES ===== */

h1 {
  font-size: 1.8em;
  font-weight: 700;
  margin: 0 0 10px 0;
  color: #ffffff;
  
  @media (max-width: 768px) {
    font-size: 1.4em;
    margin-bottom: 8px;
  }
  
  @media (max-width: 480px) {
    font-size: 1.2em;
    margin-bottom: 6px;
  }
  
  @media (max-width: 320px) {
    font-size: 1.1em;
    margin-bottom: 5px;
  }
}

.subtitle {
  font-size: 1em;
  color: #c7c7c7;
  margin-bottom: 15px;
  
  @media (max-width: 768px) {
    font-size: 0.85em;
    margin-bottom: 12px;
  }
  
  @media (max-width: 480px) {
    font-size: 0.8em;
    margin-bottom: 10px;
  }
  
  @media (max-width: 320px) {
    font-size: 0.75em;
    margin-bottom: 8px;
  }
}

.social-icons {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;

  @media (max-width: 768px) {
    gap: 8px;
  }

  @media (max-width: 480px) {
    gap: 6px;
  }
}

.icon {
  font-size: 3em;
  color: #ffffff;
  cursor: pointer;
  transition: color 0.3s;
  margin-top: -5px;
  
  @media (max-width: 768px) {
    font-size: 1.4em;
  }
  
  @media (max-width: 480px) {
    font-size: 1.8em;
  }
  
  @media (max-width: 320px) {
    font-size: 1.1em;
  }
}

.icon:hover {
  color: #61dafb;
}

/* Resume button styling - Neon Glowing Button */
:root {
  --glow-color: hsl(143, 92%, 44%);
}

.button-85 {
  position: relative;
  color: var(--glow-color);
  cursor: pointer !important;
  padding: 0.6em 2em;
  border: 0.15em solid var(--glow-color);
  border-radius: 0.45em;
  background: none;
  perspective: 3.5em;
  font-size: 1em;
  
  -webkit-box-shadow: inset 0px 0px 0.5em 0px var(--glow-color),
    0px 0px 0.5em 0px var(--glow-color);
  -moz-box-shadow: inset 0px 0px 0.5em 0px var(--glow-color),
    0px 0px 0.5em 0px var(--glow-color);
  box-shadow: inset 0px 0px 0.5em 0px var(--glow-color),
    0px 0px 0.5em 0px var(--glow-color);
  //animation: border-flicker 2s linear infinite;
  
  @media (max-width: 768px) {
    margin-top: 5px;
  }
  
  @media (max-width: 480px) {
    padding: 0.3em 0.8em;
    font-size: 0.75em;
  }
  
  @media (max-width: 320px) {
    padding: 0.3em 0.6em;
    font-size: 0.7em;
  }
}

.button-85::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0.7;
  filter: blur(1em);
  transform: translateY(120%) rotateX(95deg) scale(1, 0.35);
  background: var(--glow-color);
  pointer-events: none;
}

.button-85::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  z-index: -1;
  background-color: var(--glow-color);
  box-shadow: 0 0 2em 0.2em var(--glow-color);
  transition: opacity 100ms linear;
}

.button-85:hover {
  color: rgba(0, 0, 0, 0.8);
  text-shadow: none;
  animation: none;
}

.button-85:hover .faulty-letter {
  animation: none;
  text-shadow: none;
  opacity: 1;
}

.button-85:hover:before {
  filter: blur(1.5em);
  opacity: 1;
}

.button-85:hover:after {
  opacity: 1;
}

@keyframes faulty-flicker {
  0% {
    opacity: 0.1;
  }
  2% {
    opacity: 0.1;
  }
  4% {
    opacity: 0.5;
  }
  19% {
    opacity: 0.5;
  }
  21% {
    opacity: 0.1;
  }
  23% {
    opacity: 1;
  }
  80% {
    opacity: 0.5;
  }
  83% {
    opacity: 0.4;
  }
  87% {
    opacity: 1;
  }
}

@keyframes text-flicker {
  0% {
    opacity: 0.1;
  }
  2% {
    opacity: 1;
  }
  8% {
    opacity: 0.1;
  }
  9% {
    opacity: 1;
  }
  12% {
    opacity: 0.1;
  }
  20% {
    opacity: 1;
  }
  25% {
    opacity: 0.3;
  }
  30% {
    opacity: 1;
  }
  70% {
    opacity: 0.7;
  }
  72% {
    opacity: 0.2;
  }
  77% {
    opacity: 0.9;
  }
  100% {
    opacity: 0.9;
  }
}

@keyframes border-flicker {
  0% {
    opacity: 0.1;
  }
  2% {
    opacity: 1;
  }
  4% {
    opacity: 0.1;
  }
  8% {
    opacity: 1;
  }
  70% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

@media only screen and (max-width: 600px) {
  .button-85 {
    font-size: 1em;
  }
}

.social-section {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  gap: 10px;
  margin-top: 10px;

  @media (max-width: 768px) {
    gap: 6px;
    margin-top: 8px;
  }

  @media (max-width: 480px) {
    gap: 4px;
    margin-top: 6px;
  }
}

.wave {
  display: inline-flex;         /* keeps it in-flow with text but a stable box */
  align-items: center;
  justify-content: center;
  width: 1.15em;                /* fixed box width relative to font-size */
  height: 1.15em;               /* fixed box height */
  line-height: 1;               /* no extra line spacing */
  vertical-align: middle;       /* aligns with text baseline */
  transform-origin: center center; /* rotate about the center of the box */
  will-change: transform;       /* hint to browser for smoother animations */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  font-size: 1.6rem;            /* control visual size (adjust as needed) */
  /* optional: prevents any margin/jump from subpixel rounding */
  box-sizing: content-box;
  padding: 0;

  @media (max-width: 768px) {
    font-size: 1em !important;
    width: 1.0em;
    height: 1.0em;
  }

  @media (max-width: 480px) {
    font-size: 1.05em !important;
    width: 1.0em;
    height: 1.0em;
  }
}


@keyframes wave-rotate {
  0%   { transform: rotate(0deg); }
  10%  { transform: rotate(14deg); }
  20%  { transform: rotate(-8deg); }
  30%  { transform: rotate(14deg); }
  40%  { transform: rotate(-4deg); }
  50%  { transform: rotate(10deg); }
  60%  { transform: rotate(0deg); }
  100% { transform: rotate(0deg); }
}

.nameHeader {
  white-space: nowrap;
  display: inline-flex;
  align-items: center;
  gap: 0.25em; // adds a small space between the name and emoji
}